html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
  overflow-x: hidden;
  overflow-y: scroll;
}

body.active { overflow: hidden; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a { 
    color: inherit;
    text-decoration: none;
 }


/*----------- Main CSS Here --------------*/

/*----------- Nav CSS Here --------------*/

nav {
    top: 0;
    width: 100vw;
    height: 100px;
    display: flex;
    position: absolute;
    justify-content: space-between;
    color: white;
}

nav h2 {
    font-size: 20px;
    letter-spacing: 2.5px;
    /*font-family: "Dancing Script", cursive;*/
}

nav section {
    height: 100%;
    display: flex;
    align-items: center;
    text-align: center;
}

nav section:nth-child(1) {
    width: 50%;
    display: flex;
    justify-content: flex-start;
    padding-left: 5%;
}

nav section:nth-child(2) {
    width: 50%;
    display: flex;
    justify-content: flex-end;
}

nav h5 {
    transform: translateX(200px);
    animation: fadeInRight 1s ease-out forwards;
}

.nav-icon, nav h5 a {
    margin: 0 10px;
    cursor: pointer;
    color: white;
    position: relative;
    transition: 0.5s;
}

.nav-icon:hover {
    color: silver;
    transition: 0.5s;
}

nav h5 a::after, #burguer h5 a::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -2px; /* ligeramente debajo del texto */
    width: 100%;
    height: 2px;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.3s ease;
}

nav h5 a::after { background-color: white; }

#burguer h5 a::after { background-color: black; }

nav h5 a:hover::after, #burguer h5 a:hover::after {
  transform: scaleX(1);
}

#plus {
    display: none;
    position: fixed;
    top: 20px;
    right: 5%;
    cursor: pointer;
    color: rgb(0, 0, 0, 0.75);
    z-index: 3;
    transition: 0.5s ease;
}

#plus.active {
    transform: rotate(225deg);
    transition: 0.5s ease;
}

#burguer {
    opacity: 0;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    display: flex; 
    position: fixed;
    background-image: url("./images/liquid-cheese (1).svg");
    background-size: cover;          
    background-position: center;     
    background-repeat: no-repeat;    
    background-attachment: fixed; 
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: 1s ease;
}

#burguer.active {
    opacity: 1;
    z-index: 2;
    transition: 1s ease;
}

#burguer h5 a {
    cursor: pointer;
    color: black;
    position: relative;
    transition: 0.5s;
    font-size: 25px;
}

#burguer h4 { margin-top: 50px; }

#burguer .nav-icon {
    color: black;
    font-size: 18px;
    transition: 0.5s ease;
}

#burguer .nav-icon:hover {
    color: silver;
    transition: 0.5s ease;
 }

@media (max-width: 750px) {
    nav { display: none; }
    #plus { display: block; }
}


/*----------- Header CSS Here --------------*/

header > section {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 80px 5%;
    color: white;
}

header > section div { 
    width: auto;
    height: auto;
    padding: 20px;
   
}

header h1 { 
    margin: 0 0 -10px 0 0; 
    transform: translateX(200px);
    animation: fadeInRight 1s ease-out forwards;
}

header h4 { 
    width: 90%;
    max-width: 450px; 
    font-weight: 400;
    transform: translateX(-200px);
    animation: fadeInLeft 1s ease-out forwards;
}

/*----------- Main Theme CSS Here --------------*/

.mainTheme {
    background-image: url("./images/liquid-cheese (1).svg");
    background-size: cover;          
    background-position: center;     
    background-repeat: no-repeat;    
    background-attachment: fixed; 
}


/*----------- Career CSS Here --------------*/

.career, .galleryBox, .media {
    width: 100vw;
    height: auto;
    min-height: 100vh;
    padding-top: 50px;
    padding-bottom: 25px;
}

.career-reel {
    width: 100vw;
    height: 200px;
    display: flex;
    align-items: center;
}

.career-reel h1 {
    font-size: 85px;
}

.career-reel h1 span {
    color: transparent;                 /* Hacer el interior transparente */
    -webkit-text-stroke: 2px black;    /* Añadir contorno blanco */
    transition: 0.5s ease;
}

.career-reel h1 span:hover {
    color: white;              /* Hacer el interior transparente */
    transition: 0.5s ease;
}

.lists {
    width: 100vw;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.list {
    width: 40%;
    height: auto;
    min-width: 400px;
    padding: 0 5%;
}

.list h1 {
    font-size: 25px;
    padding-left: 15px;
}

.list ul {
    margin-top: -10px;
    font-size: 18px;
}

.list li {
    margin: 20px 0;
    font-size: 18px;
}

.showreel {
    width: 100vw;
    height: auto;
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
}

.showreel > div {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.showreel-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0 5vw;
}

.text {
  font-size: 8.5vw;
  letter-spacing: -0.01em;
  line-height: 100%;
  margin: 0 0 2vh 0;
  width: 100%;
  color: rgba(182, 182, 182, 0.2);
  background: linear-gradient(to right, #b6b6b6, #b6b6b6) no-repeat;
  -webkit-background-clip: text;
  background-clip: text;
  background-size: 0%;
  transition: background-size 0.5s cubic-bezier(0.1, 0.5, 0.5, 1);
  border-bottom: 1px solid #2F2B28;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.text span {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  color: white;
  clip-path: polygon(0 50%, 100% 50%, 100% 50%, 0 50%);
  transform-origin: center;
  transition: all 0.4s cubic-bezier(0.1, 0.5, 0.5, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.4em;
  font-weight: bold;
}

.text:hover > span {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
}

.showreel iframe {
    width: 75%;
    aspect-ratio: 16 / 10;
}

@media (max-width: 650px) {
    .video { margin: -15vh 0 25vh 0; }
}


@media (max-width: 750px) {
    .career-reel { margin: 25px 0 50px; }
    .video { margin-top: 0vh; }
}


@media (min-width: 750px) {
    .career-reel { width: 200vw; }
}


@media (max-width: 1000px) {
    .showreel { margin: 5% 0 15% 0; }
    .showreel > div { width: 90%; }
    .text { font-size: 12vw; }
}


/*----------- Gallery CSS Here --------------*/


.galleryBox {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.galleryBox > div {
    width: 45%;
    min-width: 400px;
}

.gallery, .gallery > div {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}


.gallery img {
    width: 20%;
    min-width: 150px;
    height: auto;
    margin: 2.5px;
    aspect-ratio: 5 / 8;
    object-fit: cover;
    cursor: pointer;
}

.skillset { margin-top: 20px; }

.skillset ul {
    margin-top: -10px;
    margin-bottom: 50px;
}

.skillset ul, .skillset h1 { margin-left: 15px; }

#main-gallery {
    top: 0;
    width: 100vw;
    height: 100vh;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.9);
    z-index: 5;
    color: white;
    display: none;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}

#main-gallery.active { display: flex; }

#main-gallery div {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.plus {
    top: 25px;
    right: 4vw;
    position: fixed;
    transform: rotate(45deg);
}

.button { cursor: pointer; }

#main-gallery div:nth-child(even) { width: 75vw; }

#main-gallery div:nth-child(odd) { width: 12.5vw; }

#main-gallery img {
    width: 30%;
    min-width: 150px;
    height: auto;
    margin: 2.5px;
    aspect-ratio: 5 / 8;
    object-fit: cover;
    user-select: none;
    -webkit-user-drag: none;
    -webkit-tap-highlight-color: transparent;
    outline: none;
}

@media (max-width: 650px) {
    .galleryBox { 
        padding-top: 0px;
        margin-top: -200px;
     }
}

@media (max-width: 950px) {
    #main-gallery img { width: 70%; }
}

@media (max-width: 1250px) {
    .galleryBox { 
        padding-top: 0px;
        margin-top: -100px;
     }
    .galleryBox > div { width: 90vw; }

    .gallery h1 { font-size: 25px; }

    .skillset { padding: 50px 10%; }
}

/*----------- Media & Contact CSS Here --------------*/

.media {
    display: flex;
    align-items: center;
    justify-content: center;
}

.media > div { width: 90%; }

#contact > div:nth-child(1) {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: -25px;
}

.media h1 {
    padding: 200px 0 0 0;
    text-align: center;
    font-size: 35px;
}

.media ul {
    list-style: none;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-left: -35px;
}

.media li {
    text-align: center;
    margin: 10px 20px;
}

.media li h3 {
    font-size: 20px;
}

.media li h2 {
    margin-top: -15px;
    font-size: 25px;
}

.contact {
    padding: 0 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.contact > div {
    width: 33.3%;
    display: flex;
    min-width: 200px;
    align-items: center;
    justify-content: center;
}

.contact h4 {
    font-size: 18px;
    margin-bottom: -15px;
}

.contact h5 { font-size: 15px; }

.contact > div:nth-child(1) > div { margin-left: 10px; }

.contact > div:nth-child(2), .contact > div:nth-child(3) > div { text-align: center;}

.contact img {
    width: 100px;
    margin-top: -35px;
    transform: rotate(-5deg);
}

@media (max-width: 1500px) {
    .media { margin-top: -25vh; }
}

@media (max-width: 850px) {
    .contact > div { width: 50%; }
}

@media (max-width: 600px) {
    .contact > div { width: 90%; }
    .contact > div:nth-child(1) > div { text-align: center; }
}


/*----------- CSS Animations --------------*/

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(200px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-200px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}